/*Layout*/
/** Media Queries **/
.main {
  background-image: url(/../imagenes/fondopantalla.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 800%;
}

.header {
  width: 100%;
}
.header .menu-mobile {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}
.header h3 {
  text-align: left;
  font-size: 4.3rem;
  line-height: 0.5;
}
@media (min-width: 768px) {
  .header h3 {
    padding: 0rem;
  }
}
.header__nav {
  margin: 3rem 0rem;
  display: flex;
  flex-direction: column;
  padding: 1rem;
  font-size: 2.5rem;
}
.header__nav .menu {
  display: inline;
  font-size: 3rem;
}
@media (min-width: 768px) {
  .header__nav .menu {
    display: none;
  }
}
@media (min-width: 768px) {
  .header__nav {
    padding: 2rem 8rem;
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__nav h3 {
  color: #807070;
  margin: 0;
}
.header__nav span {
  font-size: 1.6rem;
  font-weight: normal;
  margin: 0;
}
.header__nav a {
  color: #807070;
}
.header__nav .enlaces {
  display: none;
  margin-top: 3rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-weight: bold;
}
.header__nav .enlaces a {
  padding: 1rem 6rem;
  border-radius: 0.5rem;
}
.header__nav .enlaces a:hover {
  background-color: #3e6d6d;
  color: white;
}
.header__nav .enlaces.mostrar {
  display: flex;
  justify-content: center;
}
.header__nav .enlaces.mostrar a {
  padding: 1rem 6rem;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .header__nav .enlaces {
    display: flex;
    margin-top: 0;
    flex-direction: row;
  }
}

section h3 {
  color: #6c6368;
}
section .contenido__section {
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(1, 1fr);
  margin: 2rem;
}
@media (min-width: 768px) {
  section .contenido__section {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(2, 1fr);
    padding: 5rem;
    margin: 2rem;
  }
}
section .contenido__section .contenido__izquierdo p {
  color: #6c6368;
  line-height: 2;
  font-weight: bold;
}
section .contenido__section .contenido__izquierdo .boton__boton {
  display: flex;
  justify-content: space-between;
  gap: 2rem;
  flex-wrap: wrap;
}
@media (min-width: 768px) {
  section .contenido__section .contenido__izquierdo .boton__boton {
    display: flex;
    gap: 2rem;
  }
}
section .contenido__section .contenido__izquierdo .boton__boton .boton {
  background-color: #3e5b4b;
  padding: 1.5rem 4rem;
  color: #dce0e6;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  margin: 5rem 0;
  width: auto;
  border-radius: 0.5rem;
  align-self: flex-start;
  margin: 0;
  font-weight: bold;
}
@media (min-width: 768px) {
  section .contenido__section .contenido__izquierdo .boton__boton .boton {
    width: 100%;
  }
}
section .contenido__section .contenido__izquierdo .boton__boton .boton:hover {
  background-color: #293d32;
  cursor: pointer;
}
@media (min-width: 768px) {
  section .contenido__section .contenido__izquierdo .boton__boton .boton {
    width: auto;
  }
}
section .contenido__section .contenido__izquierdo .boton__boton .boton2 {
  background-color: #7d493f;
  padding: 1.5rem 4rem;
  color: #dce0e6;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
